class MyPokemonSprites extends HTMLElement {
  constructor() {
    super();
    const shadow = this.attachShadow({ mode: 'open' });
    const p = document.createElement('p');
    p.textContent = 'Fetching Pokémon sprites...';
    shadow.appendChild(p);

    const url = this.getAttribute('data-url');
    fetch(url)
      .then(response => response.json())
      .then(data => {
        const frontSprite = document.createElement('img');
        frontSprite.src = data.sprites.front_default;
        const backSprite = document.createElement('img');
        backSprite.src = data.sprites.back_default;
        shadow.appendChild(frontSprite);
        shadow.appendChild(backSprite);
        p.textContent = `Sprites for: ${data.name}`;
      })
      .catch(error => {
        p.textContent = `Error: ${error.message}`;
      });
  }
}

customElements.define('my-pokemon-sprites', MyPokemonSprites);
